"use client";

import Link from "next/link";
import Image from "next/image";
import type { Book } from "@/lib/types";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { useToast } from "@/hooks/use-toast";
import { BookCheck } from "lucide-react";

export default function BookCard({ book, hasAccess = false }: { book: Book, hasAccess?: boolean }) {
  const { toast } = useToast();

  const handleAddToCart = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.preventDefault();
    toast({
      title: "Added to Cart!",
      description: `${book.title} has been added to your shopping cart.`,
    });
  };

  return (
    <Card className="flex flex-col overflow-hidden transition-all hover:shadow-lg">
      <Link href={`/books/${book.id}`} className="block overflow-hidden group">
        <CardHeader className="p-0 relative">
          <Image
            src={book.imageUrl}
            alt={`Cover of ${book.title}`}
            width={400}
            height={600}
            className="aspect-[2/3] w-full object-cover transition-transform duration-300 group-hover:scale-105"
            data-ai-hint="book cover"
          />
          {hasAccess && (
            <div className="absolute inset-0 bg-black/50 flex items-center justify-center">
                <BookCheck className="h-12 w-12 text-white" />
            </div>
          )}
        </CardHeader>
        <CardContent className="flex-grow p-4">
          <Badge variant="secondary" className="mb-2">
            {book.category}
          </Badge>
          <CardTitle className="text-lg font-headline group-hover:text-primary">
            {book.title}
          </CardTitle>
          <CardDescription className="mt-1">{book.author}</CardDescription>
        </CardContent>
      </Link>
      <CardFooter className="flex justify-between items-center p-4 pt-0">
        <p className="text-xl font-semibold text-primary">${book.price.toFixed(2)}</p>
        {hasAccess ? (
             <Button variant="secondary" disabled>In Your Library</Button>
        ) : (
            <Button onClick={handleAddToCart} disabled={book.stock === 0}>
              {book.stock > 0 ? "Add to Cart" : "Out of Stock"}
            </Button>
        )}
      </CardFooter>
    </Card>
  );
}
